import React, {useEffect, useState} from 'react';
import axios from "axios";
import { useCookies } from "react-cookie";

function TestComponent() {
    const [printText, setPrintText] = useState();
    const [cookies] = useCookies(['XSRF-TOKEN']);
    const [csrfToken, setCsrfToken] = useState('');

    useEffect(() => {
        getPrintText();
    }, []);

    const getPrintText = async () => {
        await axios.get('/api/main', {
            headers: {'Content-Type' : 'application/json'},
            withCredentials: true,
        })
            .then(res => {
                console.log('test Text res : ', res);
                setPrintText(res.data);
                setCsrfToken(res.headers.x_csrf_token);
            })
            .catch(err => {
                console.error(err);
            })
    }

    const handleSubmit = async () => {
        console.log('cookies : ', cookies['XSRF-TOKEN']);
        console.log('csrf token : ', csrfToken);
    }



    return (
        <div>
            return : {printText}
            <div>
                <button type={'button'} onClick={handleSubmit}>button</button>
            </div>
        </div>
    )
}

export default TestComponent;